.react-chatbot-kit-chat-container {
  text-align: center;
  width: 275px;
  /*chat bot width*/
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.react-chatbot-kit-chat-inner-container {
  height: 70vh;
  /*chatbot height*/
  background-color: #fff;
  border-radius: 3px;
  border-radius: 5px; }

.react-chatbot-kit-chat-bot-message-container {
  /* background-color: #585858; */
  margin: 10px 5px;
  width: 100%;
  /* clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 10% 99%, 0% 50%, 10% 0); */
  content: "";
  display: table;
  clear: both; }

.react-chatbot-kit-chat-bot-avatar-container {
  float: left;
  /* margin: 5%; */
  width: 15%;
  background-color: #95a5a6;
  border-radius: 50%;
  align-items: center; }

.react-chatbot-kit-chat-bot-avatar-letter {
  text-align: center; }

.react-chatbot-kit-chat-bot-message {
  background-color: #95a5a6;
  clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 10% 100%, 0% 50%, 10% 0);
  float: left;
  width: 85%;
  height: 100%; }

span {
  display: block;
  padding: 0px 3px 0px 1rem; }

.react-chatbot-kit-user-chat-message-container {
  margin: 10px;
  clear: both;
  display: table;
  content: " ";
  align-items: center; }

.react-chatbot-kit-user-chat-message {
  display: inline-block;
  width: 75%;
  right: 75%;
  height: 100%;
  background-color: #25254b;
  clip-path: polygon(0% 0%, 90% 0, 100% 50%, 90% 100%, 0% 100%);
  color: #fff; }

.react-chatbot-kit-user-avatar-container {
  border-radius: 50%; }

.react-chatbot-kit-user-avatar {
  background-color: 006d77;
  clip-path: circle(50% at 50% 50%);
  width: 20%;
  float: right;
  right: 100%; }

.react-chatbot-kit-chat-header {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  background-color: #284b63;
  font-family: Arial;
  display: flex;
  align-items: center;
  font-size: 0.85rem;
  color: white;
  padding: 12.5px;
  font-weight: bold; }

.react-chatbot-kit-chat-input-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex; }

.react-chatbot-kit-chat-message-container {
  padding: 0 15.5px 10px 15.5px;
  overflow: scroll;
  height: 65vh;
  /*scrool height*/ }

.react-chatbot-kit-chat-input {
  width: 100%;
  padding: 12.5px;
  border: none;
  font-size: 0.85rem;
  border-top: 1px solid #d8d8d8;
  border-bottom-left-radius: 5px; }

.react-chatbot-kit-chat-input-form {
  width: 100%;
  display: flex; }

.react-chatbot-kit-chat-input::placeholder {
  color: #585858; }

.react-chatbot-kit-chat-btn-send {
  background-color: #2898ec;
  width: 100px;
  border: none;
  color: #fff;
  border-bottom-right-radius: 5px; }

.react-chatbot-kit-chat-btn-send-icon {
  fill: #fff;
  width: 15px;
  margin: 0 auto; }

.row {
  padding: 5rem;
  max-width: 114rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  margin-left: auto;
  margin-right: auto; }
  .row::after {
    content: "";
    display: table;
    clear: both; }
  .row [class^="col-"] {
    background: red;
    float: left;
    width: calc((100% - 2 * (8rem)) / 3);
    max-height: 20vh;
    margin-bottom: 2rem; }
    .row [class^="col-"]:not(:last-child) {
      margin-right: 8rem; }

.home {
  position: absolute;
  margin: 0;
  margin-top: 80px;
  padding: 0;
  width: 100%;
  box-sizing: border-box;
  z-index: -1;
  background-color: rgba(245, 247, 244, 0.5); }

.header {
  background-image: linear-gradient(to right bottom, #32a6a8, rgba(50, 166, 168, 0.5)), url(./image/pills.jpg);
  height: 80vh;
  width: 100%;
  background-position: center;
  background-size: cover;
  align-items: center;
  position: relative;
  display: block; }
  .header .header_main {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 4rem; }

.about_sec {
  margin: 2rem;
  display: block;
  width: 100%;
  justify-content: center; }
  .about_sec .heading2 {
    text-align: center;
    font-size: 25px; }
  .about_sec .about_content {
    margin: 0rem 4rem;
    font-size: 1.2rem;
    justify-content: center; }
    .about_sec .about_content p {
      text-indent: 2rem;
      align-items: center;
      justify-content: center;
      line-height: 2rem; }

.advantages {
  height: auto; }

details > summary {
  height: 3rem;
  margin: 0rem 6rem 0.75rem;
  font-size: 1.5rem;
  background-color: rgba(128, 128, 128, 0.25);
  border-radius: 0.5rem; }

details > p {
  padding: 0rem 2rem 0rem 2rem;
  margin: 1rem 8rem;
  font-size: 1rem;
  line-height: 2rem; }
  @media (max-width: 400px) {
    details > p {
      padding: 0 0 0 0;
      margin: 1rem 2rem 1rem 6rem; } }

.contact {
  margin: 2rem 0 0 0;
  padding: 1rem;
  background-color: #457b9d;
  position: relative;
  height: 70vh; }
  @media (max-width: 400px) {
    .contact {
      height: 40rem; } }
  .contact .contact_heading {
    text-align: center; }
  .contact .form {
    display: inline-block;
    width: 40%;
    position: absolute;
    left: 2rem;
    background-color: white;
    border-radius: 1rem;
    margin-top: 2rem; }
    .contact .form h1 {
      color: #457b9d;
      text-align: center; }
    .contact .form input {
      display: block;
      width: 90%;
      height: 2rem;
      background-color: rgba(69, 123, 157, 0.5);
      border-radius: 0.5rem; }
    .contact .form .form2 {
      padding: 1rem 2rem 1rem 2rem; }
      .contact .form .form2 [class^="form2_sec"] {
        padding: 0.5rem; }
      .contact .form .form2 .btn_submit {
        height: 2rem;
        margin: 1rem 0;
        padding: 0 auto; }
  .contact .contact_logo {
    position: absolute;
    left: 50%;
    display: inline-block;
    height: 50vh;
    width: 50%; }
    .contact .contact_logo img {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 100%; }

.navbar {
  position: fixed;
  width: 100%;
  background-color: #060b26;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
  float: left; }

.menu-bar {
  margin-left: 2rem;
  font-size: 2rem;
  background: none; }

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms; }

.nav-menu.active {
  left: 0;
  transition: 350ms; }

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px; }

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px; }

.nav-text a:hover {
  background-color: #1a83ff; }

.nav-menu-items {
  width: 100%; }

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  justify-content: start;
  align-items: center; }

span {
  margin-left: 16px; }

.products {
