.App {
  margin: 0;
  padding: 0;
  display: border-box; }

.home,
.chat,
.search {
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  box-sizing: border-box; }

.chat {
  z-index: -1;
  position: absolute;
  margin: 0;
  margin-top: 80px;
  background-color: #284b63;
  width: 100%;
  height: 100%; }
